$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Block Directory
.wp-block-ilb-icon-list {
	text-align: center;

	* {
		box-sizing: border-box;
	}

	.ilbIconList {
		max-width: 100%;
		display: inline-block;
		width: 600px;
		background: #0000;
		padding: 30px 25px;
		box-shadow: 0px 0px 10px 0px #4527a480;

		// &.theme1, &.theme2{
		// 	display: inline-flex;
		// 	align-items: center;
		// 	justify-content: space-between;

		// 	.contacts{
		// 		max-width: 50%;
		// 	}
		// }
		// &.theme2{
		// 	align-items: flex-start;
		// 	flex-direction: row-reverse;
		// }

		&.left {
			.header {
				justify-items: start;
			}
		}

		&.center {
			text-align: center;

			.header {
				justify-items: center;
			}

			ul.lists {
				display: inline-block;
			}
		}

		&.right {
			.header {
				justify-items: end;
			}

			ul.lists .list {
				flex-direction: row-reverse;
			}
		}

		.header {
			display: grid;
			height: min-content;
			margin-bottom: 30px;

			.title,
			.description {
				margin: 0 0 5px 0;
				text-align: initial;
				width: auto;
			}

			.title {
				font-size: 30px;
				font-weight: 700;
				text-transform: uppercase;
				color: #4527a4;
			}

			.description {
				font-size: 18px;
				font-weight: 500;
				color: #828282;
			}

			.separator {
				width: 20%;
				text-align: initial;
				display: inline-block;
				border-top: 2px solid #828282;
			}
		}

		ul.lists {
			list-style-type: none;
			padding: 0;
			margin: 0;

			li {
				display: flex;
				align-items: center;
				gap: 15px;
				padding: 0;

				&:not(:last-child) {
					margin-bottom: 8px;
				}

				.icon {
					font-size: 18px;
					font-style: normal;
					width: 30px;
					height: 30px;
					color: #fff;
					background: #4527a4;
					display: flex;
					align-items: center;
					justify-content: center;
					border-radius: 50%;
				}

				.link {
					display: inline-block;
					text-decoration: none;
				}

				.text {
					max-width: calc(100% - 45px);
					text-align: left;
					color: #828282;
					margin: 0;
				}
			}
		}
	}
}